@import "../../styles/utils/mixins";

.Modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;

  &__content {
    width: 350px;
    background-color: #fff;
    border-radius: 0.5rem;
  }

  &__body {
    padding: 10px;
  }
}
